import React, { ChangeEvent, useEffect, useRef, useState } from 'react'
import { Idata } from './module/Idata'
import { Cell, Pie, PieChart, Tooltip } from 'recharts'
import { IchartData } from './module/IchartData';
import DonutLargeIcon from '@mui/icons-material/DonutLarge';
import TableChartIcon from '@mui/icons-material/TableChart';
import { Doughnut } from 'react-chartjs-2';
import { Chart } from 'chart.js';
import { json } from 'stream/consumers';
import { colors } from '@mui/material';



const Dashborad = () => {
    const [data, setdata] = useState<Idata[]>([
        {
            campaigns: "cosmetics",
            clicks: 712,
            cost: "USD 4,272",
            converstions: 8,
            revenue: "USD 16,568"
        },
        {
            campaigns: "serums",
            clicks: 3961,
            cost: "USD 27,331",
            converstions: 115,
            revenue: "USD 362,526"
        },
        {
            campaigns: "facewash",
            clicks: 9462,
            cost: "USD 77,831",
            converstions: 123,
            revenue: "USD 266,800"
        },
        {
            campaigns: "shampoos",
            clicks: 439,
            cost: "USD 2,151",
            converstions: 5,
            revenue: "USD 11,029"
        },
        {
            campaigns: "conditioners",
            clicks: 1680,
            cost: "USD 3,864",
            converstions: 49,
            revenue: "USD 175,245"
        },
        {
            campaigns: "facewash 2",
            clicks: 4978,
            cost: "USD 29,370",
            converstions: 189,
            revenue: "USD 623,106"
        },


    ])

    //======== for chart=========
    const [chartData, setchartData] = useState<IchartData[]>([
        {
            groups: "Male",
            clicks: 348,
            cost: "USD 12,528",
            converstions: 42,
            revenue: "USD 62,118"
        },
        {
            groups: "Female",
            clicks: 692,
            cost: "USD 24,912",
            converstions: 35,
            revenue: "USD 5,175"
        },
        {
            groups: "Unknown",
            clicks: 105,
            cost: "USD 3,943",
            converstions: 3,
            revenue: "USD 4,489"
        },

    ])

    // ==========pichart==========
    const [percentages, setPercentages] = useState<IchartData[]>([]);

    useEffect(() => {
        // Calculate percentages when chartData changes
        const totalClicks = chartData.reduce((total, item) => total + item.clicks, 0);
        const totalCost = chartData.reduce((total, item) => total + parseFloat(item.cost.replace("USD ", "").replace(",", "")), 0);
        const totalConversions = chartData.reduce((total, item) => total + item.converstions, 0);

        const calculatedPercentages = chartData.map(item => ({
            ...item,
            percentage: totalClicks + totalCost + totalConversions > 0
                ? ((item.clicks + parseFloat(item.cost.replace("USD ", "").replace(",", "")) + item.converstions) / (totalClicks + totalCost + totalConversions)) * 100
                : 0,
        }));

        setPercentages(calculatedPercentages);

    }, [chartData]);


    // // Calculate the total of all relevant columns
    
    // Calculate the percentage for each group based on the sum of relevant columns
    
    
        const totalClicks = chartData.reduce((total, item) => total + item.clicks, 0);
        const totalCost = chartData.reduce((total, item) => total + parseFloat(item.cost.replace("USD ", "").replace(",", "")), 0);
        const totalConversions = chartData.reduce((total, item) => total + item.converstions, 0);
        
        const updatedChartData = chartData.map(item => ({
            ...item,
            percentage: totalClicks + totalCost + totalConversions > 0
            ? ((item.clicks + parseFloat(item.cost.replace("USD ", "").replace(",", "")) + item.converstions) / (totalClicks + totalCost + totalConversions)) * 100
            : 0,
        }));


    // =====



    let [doughnut, setdoughnut] = useState<boolean>(false);
    const DChart = (): void => {
        setdoughnut(false);
        document.getElementById("icon1")?.classList.add("active");
        document.getElementById("icon2")?.classList.remove("active");
    }
    const TChart = (): void => {
        setdoughnut(true);
        document.getElementById("icon1")?.classList.remove("active");
        document.getElementById("icon2")?.classList.add("active");
    }
    const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

    const firstcon = (): React.ReactNode => {

        return (

            <table className=' table '>
                <thead>
                    <tr className='row-border'>
                        <th className='table-title'>Ad Insights</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>
                            <PieChart width={250} height={200}>
                                <Pie
                                    data={percentages}
                                    cx={100}
                                    cy={80}
                                    innerRadius={60}
                                    outerRadius={80}
                                    fill="#8884d8"
                                    paddingAngle={5}
                                    dataKey="percentage"
                                >
                                    {percentages.map((entry, index) => (
                                        <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                                    ))}
                                    <Tooltip formatter={(value) => typeof value === 'number' ? `${value.toFixed(2)}%` : value} />
                                </Pie>
                            </PieChart> 
                        </td>
                        <td className='chart-data d-flex flex-column align-items-start  justify-content-center'>
                            {updatedChartData.map(item => (
                                <span key={item.groups}>
                                    {`${item.groups}: ${item.percentage.toFixed(0)}%`}
                                </span>
                            ))}
                        </td>
                    </tr>
                </tbody>
            </table>
        )
    }
    const secondcon = (): React.ReactNode => {
        return (
            <table className='table '>
                <thead>
                    <tr>
                        <th colSpan={5} className='table-title'>Ad Insights</th>

                    </tr>
                    <tr>
                        <th scope="col">groups</th>
                        <th scope="col">clicks </th>
                        <th scope="col">cost</th>
                        <th scope="col">converstions</th>
                        <th scope="col">revenue</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        chartData.map((dd) => {
                            return (
                                <tr key={dd.groups}>
                                    <td>{dd.groups}</td>
                                    <td>{dd.clicks}</td>
                                    <td>{dd.converstions}</td>
                                    <td>{dd.cost}</td>
                                    <td>{dd.revenue}</td>
                                </tr>
                            )
                        })
                    }
                </tbody>
                <tfoot>
                    <tr>
                        <td>{Charttotals.groups}</td>
                        <td>{Charttotals.clicks}</td>
                        <td>{Charttotals.converstions}</td>
                        <td>{Charttotals.cost}</td>
                        <td>{Charttotals.revenue}</td>
                    </tr>
                </tfoot>
            </table>
        )
    }

    const sortData = (data: Idata[], option: string): Idata[] => {
        const isTotalRow = (row: Idata) => row.campaigns.toLowerCase() === 'total';

        return data.slice().sort((a, b) => {
            if (isTotalRow(a) || isTotalRow(b)) {
                return isTotalRow(a) ? 1 : -1;
            }

            const parseRevenue = (value: string | number): number => {
                if (typeof value === 'string') {
                    return parseFloat(value.replace('USD ', '').replace(',', ''));
                } else {
                    return value as number;
                }
            };

            const revenueA = parseRevenue(a.revenue);
            const revenueB = parseRevenue(b.revenue);

            if (option === 'high') {
                return revenueB - revenueA;
            } else if (option === 'low') {
                return revenueA - revenueB;
            } else {
                return 0;
            }
        });
    };



    const handleSortChange = (e: React.ChangeEvent<HTMLSelectElement>): void => {
        const selectedOption = e.target.value;
        setdata((prevData) => sortData(prevData, selectedOption));
    };


    const [isChecked, setChecked] = useState(false);

    const handleToggle = () => {
        setChecked(!isChecked);
    };


    //  ==================for total rows========

    const calculateTotals = () => {

        const totalClicks = data.reduce((acc, current) => acc + (current.clicks || 0), 0);
        const totalConverstions = data.reduce((acc, current) => acc + (current.converstions || 0), 0);
        const totalCost = data.reduce((acc, current) => acc + (typeof current.cost === 'string' ? parseFloat(current.cost.replace(/[^0-9.]/g, '')) : 0), 0);
        const totalRevenue = data.reduce((acc, current) => acc + (typeof current.revenue === 'string' ? parseFloat(current.revenue.replace(/[^0-9.]/g, '')) : 0), 0);

        return {
            campaigns: 'Total',
            clicks: totalClicks,
            cost: `USD ${totalCost.toFixed(2)}`,
            converstions: totalConverstions,
            revenue: `USD ${totalRevenue.toFixed(2)}`,
        };
    };


    const calculateTotalsChart = () => {

        const totalClicks = data.reduce((acc, current) => acc + (current.clicks || 0), 0);
        const totalConverstions = data.reduce((acc, current) => acc + (current.converstions || 0), 0);
        const totalCost = data.reduce((acc, current) => acc + (typeof current.cost === 'string' ? parseFloat(current.cost.replace(/[^0-9.]/g, '')) : 0), 0);
        const totalRevenue = data.reduce((acc, current) => acc + (typeof current.revenue === 'string' ? parseFloat(current.revenue.replace(/[^0-9.]/g, '')) : 0), 0);

        return {
            groups: "total",
            clicks: totalClicks,
            cost: `USD ${totalCost.toFixed(2)}`,
            converstions: totalConverstions,
            revenue: `USD ${totalRevenue.toFixed(2)}`
        };
    };


    const [totals, setTotals] = useState<Idata>(calculateTotals);
    const [Charttotals, setChartTotals] = useState<IchartData>(calculateTotalsChart);

    useEffect(() => {
        setTotals(calculateTotals);
        setChartTotals(calculateTotalsChart)
    }, [data]);

    return (
        <>

            <div className="container mt-5">
                <div className="row gap-5">
                    <div className="col-5 col-md-6 col-12 col-sm-8">
                        <table className="table    ">
                            <thead>
                                <tr>
                                    <th colSpan={4} className='table-title'>Ad Insights</th>
                                    <th className=''>
                                        Sort <select id='sort' onChange={handleSortChange}  >
                                            <option value=""> </option>
                                            <option value="high">high to low</option>
                                            <option value="low">Low To High</option>
                                        </select>
                                    </th>
                                </tr>
                                <tr>
                                    <th scope="col">campaigns</th>
                                    <th scope="col">clicks </th>
                                    <th scope="col">cost</th>
                                    <th scope="col">converstions</th>
                                    <th scope="col">revenue</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    data.map((dd) => {
                                        return (
                                            <tr key={dd.campaigns}>
                                                <td>{dd.campaigns}</td>
                                                <td>{dd.clicks}</td>
                                                <td>{dd.converstions}</td>
                                                <td>{dd.cost}</td>
                                                <td>{dd.revenue}</td>
                                            </tr>
                                        )
                                    })
                                }
                            </tbody>
                            <tfoot>
                                <tr>
                                    <td>{totals.campaigns}</td>
                                    <td>{totals.clicks}</td>
                                    <td>{totals.converstions}</td>
                                    <td>{totals.cost}</td>
                                    <td>{totals.revenue}</td>
                                </tr>
                            </tfoot>
                        </table>
                    </div>

                    {/* ======second table ===== */}
                    <div className="col-5 col-md-5 col-12 col-sm-8">
                        <table className='table table-manage'>
                            <tbody>
                                <tr>
                                    <td className='space-remove'>
                                        {
                                            !doughnut ? firstcon() : secondcon()
                                        }
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div className='svg'>
                                            <div className="com-svg">
                                                <DonutLargeIcon className='icon-1 active' id={"icon1"} onClick={DChart} />
                                                <TableChartIcon className='icon-1' id={"icon2"} onClick={TChart} />
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                            {/* ====== */}

                            {/* ======== */}

                        </table>
                    </div>
                </div>
            </div>


        </>
    )
}

export default Dashborad