import React, { useState } from 'react'
import { BrowserRouter, Route, Routes, useNavigate } from 'react-router-dom';
import TextAdForm from './TextAdForm';
import MediaAdForm from './MediaAdForm';

const CreateAds: React.FC = () => {

    const [first, setfirst] = useState(false)
    const [second, setsecond] = useState(false)
    
    const navigate = useNavigate()


    const ChangeData = (e: React.ChangeEvent<HTMLInputElement>) => {
        console.log(e.target.value);
        const { name, checked } = e.target;
        if (name === 'first') {
            setfirst(checked);
        } else if (name === 'second') {
            setsecond(checked);
        }
    }


    const MoveToForm = () => {
        if (first && !second) {
            // alert("You selected the Text Ad. This is the first card.");
              setTimeout(() => {
                navigate('/textform');
              }, 600);
        } else if (!first && second) {
            // alert("You selected the Media Ad. This is the second card.");
            setTimeout(() => {
                navigate('/mediaform')
              }, 600);

        } else {
            alert("Please select only one card.");
        }
    };



    return (
        <div className="container mt-5">
            <div className="row justify-content-center">
                <div className={`col-10 col-sm-8 col-md-6 col-lg-4 `} >
                    <input type="checkbox" id='card1' name={'first'} checked={first} onChange={(e) => ChangeData(e)} />
                    <label className="crd" htmlFor='card1'>
                        <img src={'https://digitalmainstreet.ca/wp-content/uploads/2020/07/google-search-ads-at-2x.png'} className="card-img-top" alt="" />
                        <div className="des">
                            <span>create</span>
                            <h4>Text Ad</h4>
                        </div>
                    </label>
                </div>
                <div className={`col-10 col-sm-8 col-md-6 col-lg-4 `}>
                    <input type="checkbox" id='card2' name={"second"} checked={second} onChange={(e) => ChangeData(e)} />
                    <label className="crd" htmlFor='card2'>
                        <img src={'https://media.licdn.com/dms/image/D4D12AQGyri1tknSt1Q/article-cover_image-shrink_720_1280/0/1683712488477?e=2147483647&v=beta&t=GS08e-eVreQLJZIHskFkzT1sUYPY_1TkwKaDebBObng'} className="card-img-top" alt="" />
                        <div className="des">
                            <span>create</span>
                            <h4>Media Ad</h4>
                        </div>
                    </label>
                </div>
            </div>
            <p className='btn-align mt-5'>
                <a href="#" className='btn btn-primary' onClick={MoveToForm} >Next</a>
            </p>
        </div>
    );
};

// export default CardSelection;




export default CreateAds;