import React from 'react'
import { Link } from 'react-router-dom'
import Swal, {} from 'sweetalert2'
const MediaAdForm = () => {

 const submitData=()=>{
  
  Swal.fire({
    position: "center",
    icon: "success",
    title: "submit",
    timer: 1500
  });
  }
  return (
    <>

<div className="container mt-5">
  <p>MediaAdForm</p>
  <div className="row">
    <div className="col-md-6">
      <form>
        <div className="form-group">
          <label htmlFor="heading1">Heading 01</label>
          <input type="text" className="form-control" id="heading1" />
        </div>

        <div className="form-group">
          <label htmlFor="heading2">Heading 02</label>
          <input type="text" className="form-control" id="heading2" />
        </div>

        <div className="form-group">
          <label htmlFor="videoUrl">Video URL</label>
          <input type="text" className="form-control" id="videoUrl" />
        </div>

        <div className="form-group">
          <label htmlFor="landscapeImage">Landscape Marketing Image</label>
          <input type="text" className="form-control" id="landscapeImage" />
        </div>

        <div className="form-group">
          <label htmlFor="squareImage">Square Marketing Image</label>
          <input type="text" className="form-control" id="squareImage" />
        </div>
      </form>
    </div>

    <div className="col-md-6">
      <form>
        <div className="form-group">
          <label>Description 1</label>
          <textarea className="form-control" rows={4}></textarea>
        </div>

        <div className="form-group">
          <label htmlFor="businessName">Business Name</label>
          <input type="text" className="form-control" id="businessName" />
        </div>

        <div className="form-group">
          <label htmlFor="buttonLabel">Button Label</label>
          <input type="text" className="form-select" id="buttonLabel" />
        </div>

        <div className="form-group">
          <label htmlFor="portraitImage">Portrait Marketing Image</label>
          <input type="text" className="form-control" id="portraitImage" />
        </div>
      </form>
    </div>

    <div className="col-12">
      <div className="form-group" style={{ paddingLeft: '20px' }}>
        <label htmlFor="websiteUrl">Website URL</label>
        <input type="text" className="form-control" id="websiteUrl" />
      </div>
    </div>

    <div className="col-12 mt-3 d-flex justify-content-end">
      <Link to={'/createads'} className="btn btn-txt  mr-2">
        Back
      </Link>
      <Link to={'/createads'} className="btn btn-primary btn-txt2" onClick={submitData}>
        Submit
      </Link>
    </div>
  </div>
</div>



    </>




  )
}

export default MediaAdForm