import React from 'react'
import { Link } from 'react-router-dom'

const Header = () => {
  return (
    <div>
         <nav className="navbar navbar-expand-sm navbar-light bg-light" >
                <div className="container">
                    <Link to={'/CreateAdsApp'} className="navbar-brand">APP LOGO</Link>
                    <button
                        className="navbar-toggler d-lg-none"
                        type="button"
                        data-bs-toggle="collapse"
                        data-bs-target="#collapsibleNavId"
                        aria-controls="collapsibleNavId"
                        aria-expanded="false"
                        aria-label="Toggle navigation" >
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="collapsibleNavId">
                        <ul className="navbar-nav justify-content-end w-100 me-auto mt-2 mt-lg-0">
                            <li className="nav-item">
                            <Link to={'/CreateAdsApp'} className="nav-link">DASHBOARD</Link>
                            </li>
                            <li className="nav-item">
                                <Link to={'/createads'} className="nav-link">CREATE ADS</Link>
                            </li>
                        </ul>

                    </div>
                </div>
            </nav>

    </div>
  )
}

export default Header